@import '../reset.css';
@import url('https://fonts.googleapis.com/css2?family=Jua&display=swap');
body {
  font-family: 'Jua', sans-serif;
}

.container {
  height: 100vh;
  display: grid;
  grid-template-rows: 1fr 10fr;
}

.clicked {
  color: #d9ff00;
}

.header_main_name {
  background-color: #b8e9ff;
  display: flex;
  gap: 25%;
  font-weight: 600;
  align-items: center;
  span {
    &:first-child {
      display: flex;
      align-items: flex-end;
    }
    &:nth-child(2) {
      display: flex;
      align-items: flex-end;
      font-size: 50px;
    }
  }
  img {
    box-sizing: border-box;
    padding: 10px 0 10px 20px;
    width: 300px;
    height: 100%;
    object-fit: contain;
  }
}

.sidebar {
  display: grid;
  grid-template-columns: 1.5fr 10fr;

  .select__menu {
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;
    background-color: #083e58;
    color: #fff;
    .h2__title {
      font-size: 30px;
    }
    .ages {
      margin-top: 10px;
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
      font-size: 22px;
      .physical__list {
        display: flex;
        flex-direction: column;
        gap: 5px;
        font-size: 18px;
        li {
          &:first-child {
            margin-top: 10px;
          }
        }
      }
      &:first-child {
        margin-top: 50px;
      }
      .custom-div {
        width: 150px;
        height: 40px;
        border-radius: 5px;
        cursor: pointer;
        transition: all 0.3s ease;
        position: relative;
        display: inline-block;
        box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, 0.5),
          7px 7px 20px 0px rgba(0, 0, 0, 0.1),
          4px 4px 5px 0px rgba(0, 0, 0, 0.1);
        outline: none;
      }

      .div-8 {
        background-color: #f0ecfc;
        background-image: #4e42f0;
        line-height: 42px;
        padding: 0;
        border: none;
        color: #000;

        span {
          position: relative;
          display: block;
          width: 100%;
          height: 100%;
          &:hover {
            color: #c797eb;
          }
          &:before,
          :after {
            position: absolute;
            content: '';
            left: 0;
            top: 0;
            background: #c797eb;
            transition: all 0.3s ease;
          }
          &:before {
            width: 2px;
            height: 0%;
          }
          &:after {
            height: 2px;
            width: 0%;
          }
          &:hover:before {
            height: 100%;
          }
          &:hover:after {
            width: 100%;
          }
        }

        &:before,
        &:after {
          position: absolute;
          content: '';
          right: 0;
          bottom: 0;
          background: #c797eb;
          transition: all 0.3s ease;
        }
        &:before {
          height: 0%;
          width: 2px;
        }
        &:after {
          width: 0%;
          height: 2px;
        }
        &:hover:before {
          height: 100%;
        }
        &:hover:after {
          width: 100%;
        }
        &:hover {
          color: #fff;
          background: transparent;
        }
      }
    }
  }
  .gif__description {
    width: 90%;
    height: 90%;
    background-color: rgb(220, 215, 215);
    display: grid;
    margin: 45px auto;
    .physical__list {
      display: flex;
      flex-direction: column;

      .physical__list-gif {
        display: flex;

        img {
          width: 300px;
          height: 550px;
          flex-grow: 1;
          object-fit: cover;
          border: 2px solid #fff;
          box-sizing: border-box;
          padding: 10px;
        }
      }
      .physical__group {
        display: flex;
        height: 100%;
        .physical__list-item {
          width: 300px;
          flex-grow: 1;
          padding: 10px;
          border: 2px solid #fff;
        }
      }
    }
  }
}
